import {Component, OnInit} from '@angular/core';
import {
  Exercise,
  GymSession,
  GymStage,
  Interval,
  Stretch,
  TrainingContentService
} from './services/training-content.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.less']
})

export class AppComponent implements OnInit {
  title = 'Fitness';

  stretches: Stretch[];
  intervals: Interval[];
  stages: GymStage[];
  exercises: Exercise[];

  constructor(private trainingContentService: TrainingContentService) {}

  ngOnInit(): void {

    this.stretches = this.trainingContentService.getStretches();
    this.intervals = this.trainingContentService.getIntervals();
    this.stages = this.trainingContentService.getGymStages();
    this.exercises = this.trainingContentService.getExercises();
  }
}
